<template>
  <div class="container">
    <el-breadcrumb class="path" separator-class="el-icon-arrow-right">
      <span style="float:left">当前位置：</span>
      <el-breadcrumb-item>文章管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/article/index' }">文章列表</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="data-container">
      <el-row class="filter-box">
        <el-col :xs="{span: 16}" :sm="{span: 14}" :md="{span: 10}" :lg="{span: 8}">
          <el-radio-group v-model="condition">
          <el-radio-button label="1">全部{{count}}</el-radio-button>
          <el-radio-button label="2">已发布{{count}}</el-radio-button>
          <el-radio-button label="3">草稿箱{{count}}</el-radio-button>
          <el-radio-button label="4">回收站{{count}}</el-radio-button>
        </el-radio-group>
        </el-col>
        <el-col :xs="8" :sm="10" :md="{span:14}" :lg="{span:15, offset: 1}">
          <el-row class="cate-filter-box" :gutter="10">
            <el-col class="hidden-sm-and-down" :md="4" :lg="3">
              <el-button icon="el-icon-refresh">刷新</el-button>
            </el-col>
            <el-col class="hidden-md-and-down"  :md="5" :lg="5">
              <el-select v-model="condition2"
                filterable
                placeholder="分类筛选">
                  <el-option-group
                    v-for="group in category"
                    :key="group.label"
                    :label="group.label">
                    <el-option
                      v-for="item in group.options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-option-group>
                </el-select>
            </el-col>
            <el-col class="hidden-md-and-down"  :md="5" :lg="5">
              <el-select v-model="condition3"
                filterable
                placeholder="标签筛选">
                  <el-option-group
                    v-for="group in tag"
                    :key="group.label"
                    :label="group.label">
                    <el-option
                      v-for="item in group.options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-option-group>
                </el-select>
            </el-col>
            <el-col class="hidden-sm-and-down"  :md="8" :lg="8">
              <el-input
                placeholder="请输入关键字搜索"
                suffix-icon="el-icon-search"
                v-model="keywords">
              </el-input>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
      <div class="list-box">
        <el-table
        ref="multipleTable"
          border
          stripe
          :data="tableData"
        v-loading="loading"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        >
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          label="ID"
          width="55">
          <template slot-scope="scope">{{ scope.$index + 1 }}</template>
        </el-table-column>
        <el-table-column
          prop="title"
          label="文章标题"
           width="300"
          show-overflow-tooltip
          >
        </el-table-column>
        <el-table-column
          prop="thumb_img"
          label="缩略图"
           width="200"
          show-overflow-tooltip
          >
        </el-table-column>
        <el-table-column
          prop="category"
          label="所属分类"
          width="200"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          label="标签"
          width="200">
          <template slot-scope="scope">
            <el-tag v-for="(item, index) in scope.row.tags" :key="index">{{item}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="comment_num"
          label="评论"
          width="80"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="like_num"
          label="喜欢"
          width="80"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="creat_date"
          label="日期"
          width="150"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="open"
          label="公开"
          width="80"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态"
          width="80"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          label="操作"
          show-overflow-tooltip>
           <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                icon="el-icon-edit"
                circle
                v-waves
                @click="handleEdit(scope.row._id)"></el-button>
                <el-button
                size="mini"
                v-waves
                type="danger"
                icon="el-icon-delete"
                circle
                @click="handleDelete(scope.row._id)"></el-button>
            </template>
        </el-table-column>
      </el-table>
      </div>
      </div>
  </div>
</template>

<script>
import waves from '@/directive/waves'
export default {
  directives: {
    waves
  },
  data () {
    return {
      loading: false,
      condition: '1',
      condition2: '',
      condition3: '',
      keywords: '',
      count: '(0)',
      tableData: [
        {
          _id: 1,
          title: '测试啊啊啊啊啊啊啊啊啊',
          thumb_img: '',
          category: '生活',
          tags: ['js', 'NodeJS', '计算机', '计算机', '计算机'],
          comment_num: '99',
          like_num: '666',
          creat_date: '2018-9-3',
          open: '否',
          status: '已发布'
        }
      ],
      category: [
        {
          label: '所有分类',
          options: [{
            value: '1',
            label: '上海'
          },
          {
            value: 'Beijing',
            label: '北京'
          },
          {
            value: 'Beijing2',
            label: '北京2'
          }
          ]
        }
      ],
      tag: [
        {
          label: '所有分类',
          options: [{
            value: '1',
            label: 'js'
          },
          {
            value: 'Beijing',
            label: 'php'
          },
          {
            value: 'Beijing2',
            label: 'java'
          }
          ]
        }
      ],
      multipleSelection: []
    }
  },
  methods: {
    handleSelectionChange (val) {
      this.multipleSelection = val
    }
  }
}
</script>

<style lang="stylus" scoped>
.data-container
  background #ddd
  margin 0 15px
  .filter-box
    padding 5px
    background #fff
    margin-bottom 5px
    .el-button
      padding 5px 10px
    .cate-filter-box
      .el-select
        width 100%
  .list-box
    padding 5px
    background #fff
    .el-tag
      height 20px
      line-height 18px
    .el-tag+.el-tag
      margin-left 10px
</style>



